
import Swal from "sweetalert2"
import { types } from "../components/types/types"
import { fetchConToken, fetchSinToken } from "../helper/fetch"
import { eventLogout } from "./events"



//esta es una tarea asincrona por lo q podemos usar el return y gracias al thunk se vuelve a disparar
export const startLogin=(email, password)=>{

    return async(dispatch)=>{

        const resp = await fetchSinToken('auth',{email,password},'POST')

        const body = await resp.json()

        if(body.ok){//este body.ok lo establecimos en el backend...tambien pudiera hacerse con el ok q da la resp

            localStorage.setItem('token',body.token) //grabo en el localstorage el token

            localStorage.setItem('token-init-date',new Date().getTime()) //grabo tambien la fecha en q se creo pq se q va a durar dos horas

            dispatch(login({
                uid:body.uid,
                name:body.name
            }))
        }else{
            Swal.fire('Error',body.msg,'error')
        }

    }
}



