export const startChecking=()=>{

    return async(dispatch)=>{

        const resp = await fetchConToken('auth/renew')

        const body = await resp.json()

        if(body.ok){//este body.ok lo establecimos en el backend...tambien pudiera hacerse con el ok q da la resp

            localStorage.setItem('token',body.token) //grabo en el localstorage el token

            localStorage.setItem('token-init-date',new Date().getTime()) //grabo tambien la fecha en q se creo pq se q va a durar dos horas

            dispatch(login({
                uid:body.uid,
                name:body.name
            }))

        }else{
            dispatch(checkingFinish())
        }
    }

}



const checkingFinish=()=>({  //para cancelar el checking
    type:types.authCheckingFinish
})


const login=(user)=>({
    type:types.authLogin,
    payload:user
})



export const startLogout=()=>{
    return(dispatch)=>{
        localStorage.clear()
        dispatch(eventLogout())
        dispatch(logout())
    }
}

const logout =()=>({
    type:types.authLogout
})
