const baseUrl=process.env.REACT_APP_API_URL

export const fetchSinToken = (endponit,data,method='GET') => {

    const url=`${baseUrl}/${endponit}`

    if(method==='GET'){  //si es un metodo get se devuelve el fetch con la direccion url solamente

        return fetch(url)

    }else { //en caso q no sea get se hace el fetch de la url y adicional se manda el tipo de metodo, el header donde se manda el contentype y el body q se manda como un stringify

        return fetch(url,{
            method,
            headers:{
                'Content-Type':'application/json'
            },
            body: JSON.stringify(data)
        })
    }

}



export const fetchConToken = (endponit,data,method='GET') => {

    const url=`${baseUrl}/${endponit}`

    const token= localStorage.getItem('token') || ''

    if(method==='GET'){  //si es un metodo get se devuelve el fetch con la direccion url solamente

        return fetch(url,{
            method,
            headers:{
                'x-token':token
            }
        })

    }else { //en caso q no sea get se hace el fetch de la url y adicional se manda el tipo de metodo, el header donde se manda el contentype y el body q se manda como un stringify

        return fetch(url,{
            method,
            headers:{
                'Content-Type':'application/json',
                'x-token':token
            },
            body: JSON.stringify(data)
        })
    }

}