import moment from "moment"

export const prepareEvents = (events=[]) => {

    return events.map((e)=>({//se recorren los eventos y por cada evento se desestructura el evento
        ...e, //se deja todo com oesta y solo se modifican las fechas
        endD:moment(e.endD).toDate(), //las fechas lsa ponemos en formato moment, entramos la fecha q queremos cambiar y le ponemos toDate
        start:moment(e.start).toDate() //esta sentencia es para q se guarden como fecha y no como string aunque es solo en el frontend
    }))
}
