import React, { useEffect, useState } from 'react'
import { Navbar } from '../ui/Navbar'
import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'
import 'moment/locale/es' //cambia el lenguaje de moment
import 'react-big-calendar/lib/css/react-big-calendar.css'
import { messages } from '../../helper/calendar-message'
import { CalendarEvent } from './CalendarEvent'
import { CalendarModal } from './CalendarModal'
import { useDispatch, useSelector } from 'react-redux';
import { uiOpenModal } from '../../actions/ui'
import { eventClearActiveEvent, eventSetActive, eventStartLoading } from '../../actions/events'
import { AddNewFab } from '../ui/AddNewFab'
import { DeleteEventFab } from '../ui/DeleteEventFab'

moment.locale('es')

const localizer = momentLocalizer(moment)


export const CalendarScreen = () => {

    const dispatch=useDispatch();
    const {events,activeEvent}=useSelector(state => state.calendar);
    const {uid}=useSelector(state => state.auth);

    const [lastView, setLastView] = useState(localStorage.getItem('lastView') || 'month')

    useEffect(() => {

        dispatch(eventStartLoading())
    
    }, [dispatch])
    
    const onDobleClick=(e)=>{
        dispatch(uiOpenModal())
    }

    const onSelectEvent=(e)=>{
        dispatch(eventSetActive(e))
    
    }

    const onViewChange=(e)=>{
        
        setLastView(e)
        localStorage.setItem('lastView',e)
    }

    const onSelectSlot=(e)=>{
        dispatch(eventClearActiveEvent())
        
    }

    const eventStyleGetter=(event, start, endD, isSelected)=>{



        const style={
            backgroundColor:(uid===event.user._id)?'#367CF7':'grey',
            borderRadius:'0px',
            opacity: 0.8,
            display:'block',
            color:'white'
        }

        return {style}
    }

    

    return (
        <div className='calendar-screen'>
            <Navbar/>

            <Calendar
                localizer={localizer}
                events={ events}
                startAccessor="start"
                endAccessor="endD"
                messages={messages}
                eventPropGetter={eventStyleGetter}
                onDoubleClickEvent={onDobleClick}
                onSelectEvent={onSelectEvent}
                onView={onViewChange}
                onSelectSlot={onSelectSlot}//para dar clic en las celdas vacias
                selectable={true}//para q las celdas vacias sean seleccionables
                view={lastView}//para q se quede en la ultima vista
                components={{
                    event:CalendarEvent
                }}
            />

            <AddNewFab />
            {
                activeEvent &&
                <DeleteEventFab/>
            }

            <CalendarModal/>    

        </div>
  )
}
