import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {Route,BrowserRouter as Router,Routes, Navigate} from 'react-router-dom'
import { startChecking } from '../actions/auth'
import { LoginScreen } from '../components/auth/LoginScreen'
import { CalendarScreen } from '../components/calendar/CalendarScreen'
import { PrivateRoute } from './PrivateRoute'
import { PublicRoute } from './PublicRoute'

export const AppRouter = () => {

    const dispatch = useDispatch();
    const {checking,uid} = useSelector( state => state.auth );

    useEffect(() => { //se crea un efecto para comprobar q el token esta activo q solo cambia cuando se hace el dispatch
    
        dispatch(startChecking()) //se hace en el app router pq en dependencia del token es las rutas a las q se podra acceder

    }, [dispatch])

    if(checking){
        return(<h5>Espere...</h5>)
    }
    
    return (
        <>
            <Router>
                <Routes>
                    <Route  path='/login' element={
                                        <PublicRoute isAuthenticated={!!uid}> 
                                                <LoginScreen/>
                                        </PublicRoute>} 
                    />

                    <Route path='/calendar' element={
                                        <PrivateRoute  isAuthenticated={!!uid}>
                                            <CalendarScreen/>
                                        </PrivateRoute>}
                    />
                    <Route path='*' element={<Navigate to='/calendar'/>} />
                </Routes>
            </Router>
        </>
    )
}

//el uid es un string si se niega una vez da falso dos veces devuelve true...si es verdadero entro a la ruta