
import { types } from "../components/types/types";
/*events:[{
        id:new Date().getTime(),
        title:'Cumple del jefe',
        start:moment().toDate(),
        end:moment().add(2,'hours').toDate(),
        notes:'Comprar dulces',
        user:{
            _id:'1234',
            name:'Daylin'
        }
    }], */

const initialState={
    events:[],
    activeEvent:null
}

export const calendarReducer = (state=initialState,action) => {

    switch (action.type) {
        case types.eventSetActive:
            
            return{
                ...state,
                activeEvent:action.payload
            }
    
        case types.eventAddNew:
            return{
                ...state,//se mantiene todo el estado y se cambia la parte de eventos
                events:[//como es un arreglo se mantienen todos los eventos anteriores y se agrega el nuevo con el action.payload
                    ...state.events,
                    action.payload
                ]
            }    
            
        case types.eventClearActiveEvent:
            return{
                ...state,
                activeEvent:null
            }   

        case types.eventUpdate:
            return{
                ...state,
                events:state.events.map(//se hace un mapp de los eventos y se busca el q el id sea igual al del action.payload
                    e=>(e.id===action.payload.id) ?action.payload :e //si son iguales  se modifica ese sino se devuelve el evento como estaba
                )
            }    

            case types.eventDeleted:
                return{
                    ...state,
                    events:state.events.filter(//se hace un filter de los eventos y se busca el q el id sea igual al del id del evento activo
                        e=>(e.id!==state.activeEvent.id) //si son iguales  se elimina
                    ),
                    activeEvent:null
                }    

            case types.eventLoaded:
                return{
                    ...state,
                    events:[...action.payload] //con el spread operator expandotodo lo q viene en el action.payload q son los eventos
                }    

            case types.eventLogout:
                return{
                    ...initialState
                }    

        default:
            return state
    }
}
