import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';
import 'react-datetime-picker/dist/DateTimePicker.css';
import 'react-calendar/dist/Calendar.css';
import 'react-clock/dist/Clock.css';
import Swal from 'sweetalert2';
import { useDispatch,useSelector } from 'react-redux';
import { uiCloseModals } from '../../actions/ui';
import { customStyles } from '../../helper/customStyles';
import {  eventClearActiveEvent, eventStartAddNew, eventStartUpdate } from '../../actions/events';



    Modal.setAppElement('#root');

    const now=moment().minutes(0).seconds(0).add(1,'hours')
    const end=now.clone().add(1,'hours')//se clona la hora q esta en now agregandole una hora mas

    const initEvent={
        title:'',
        notes:'',
        start:now.toDate(),
        endD:end.toDate()
    }

export const CalendarModal = () => {

    const dispatch=useDispatch()
    const {modalOpen }=useSelector(state=>state.ui)
    const {activeEvent }=useSelector(state=>state.calendar)
    
    const [dateStart, setDateStart] = useState(now.toDate())
    const [dateEnd, setDateEnd] = useState(end.toDate())
    const [titleValid, setTitleValid] = useState(true)

    const [formValues, setFormValues] = useState(initEvent)

    const {title,notes,start,endD}=formValues

    useEffect(() => {
        if(activeEvent){
            setFormValues(activeEvent)
        }else{
            setFormValues(initEvent)
        }
        
    }, [activeEvent])
    

    const handleInputChange=({target})=>{
        setFormValues({
            ...formValues,
            [target.name]:target.value
        })
    }

    const onChangeStartDate=(e)=>{
        setDateStart(e)
        setFormValues({
            ...formValues,
            start:e
        })
    }

    const onChangeEndDate=(e)=>{
        setDateEnd(e)
        setFormValues({
            ...formValues,
            endD:e
        })
    }

    const handleSubmitForm=(e)=>{
        e.preventDefault()
        
        const momentStart = moment(start)
        const momentEnd = moment(endD)

        if(momentStart.isSameOrAfter(momentEnd)){//si la fecha de inicio esta despues o es igual a la fecha de fin da error
            Swal.fire('Error','La fecha final debe ser mayor que la de inicio ','error');
            return;
        }
        
        if(title.trim().length<2){
            return setTitleValid(false)
        }

        if(activeEvent){
            dispatch(eventStartUpdate(formValues))
        }else{
            dispatch(eventStartAddNew(formValues))
        }
        

        setTitleValid(true)
        closeModal()
    }

    const closeModal=()=>{

        dispatch(eventClearActiveEvent())
        dispatch(uiCloseModals())
        setFormValues(initEvent)

    }

    return (
        <Modal
            isOpen={modalOpen}
            // onAfterOpen={afterOpenModal}
            onRequestClose={closeModal}
            style={customStyles}
            closeTimeoutMS={200}
            className="modal"
            overlayClassName='modal-fondo'
        >
            <h1>{activeEvent ? 'Editar Evento' :'Nuevo Evento'}  </h1>
            <hr />
        
            <form className="container" onSubmit={handleSubmitForm}>

                <div className="form-group mb-2">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker 
                    className='form-control'
                    onChange={onChangeStartDate} 
                    value={dateStart} />
                </div>

                <div className="form-group mb-2">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker 
                    className='form-control' 
                    onChange={onChangeEndDate} 
                    value={dateEnd} 
                    minDate={dateStart}
                    />
                </div>

                <hr />
                <div className="form-group mb-2">
                    <label>Titulo y notas</label>
                    <input 
                        type="text" 
                        className={`form-control ${ !titleValid && 'is-invalid'} ` }
                        placeholder="Ttulo del evento"
                        name="title"
                        autoComplete="off"
                        value={title}
                        onChange={handleInputChange}
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripcin corta</small>
                </div>

                <div className="form-group mb-2">
                    <textarea 
                        type="text" 
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={notes}
                        onChange={handleInputChange}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Informacin adicional</small>
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </Modal>
    )
}
